import React, { Component } from "react";
import { Layout, Icon } from "antd";
import ExtraContent from "./ExtraContent";
// Ant design
const { Content } = Layout;

export default class RigaCryptoSiLaponaEnigel extends Component {
  render() {
    return (
      <div className="content_wrapper">
        <Content
          style={{
            background: "#fff",
            padding: 24,
            marginTop: "3em",
            minHeight: 280
          }}
        >
          <div className="content">
            <div className="header">
              <h1>Riga Crypto i Lapona Enigel</h1>
              <h4>Ion Barbu</h4>
            </div>

            <ExtraContent />
            <div className="body">
              <div class="section poezie">
                <p>
                  Menestrel trist, mai aburit <br />
                  Ca vinul vechi ciocnit la nunt, <br />
                  De cuscrul mare druit <br />
                  Cu pungi, panglici, beteli cu funt, <br />
                </p>
                <p>
                  Mult ndrtnic menestrel, <br />
                  Un cntec larg tot mai ncearc, <br />
                  Zi-mi de lapona Enigel <br />
                  i Crypto, regele-ciupearc! <br />
                </p>
                <p>
                  - Nunta frunta! <br />
                  Ospul tu limba mi-a fript-o, <br />
                  Dar, cntecul, tot zice-l-a, <br />
                  Cu Enigel i riga Crypto. <br />
                </p>
                <p>
                  - Zi-l menestrel! <br />
                  Cu foc l-ai zis acum o var; <br />
                  Azi zi-mi-l strns, ncetinel, <br />
                  La spartul nunii, n cmar. <br />
                </p>
                <p>*</p>
                <p>
                  Des cercetat de pdurei <br />
                  n pat de ru i-n hum uns, <br />
                  mprea peste burei <br />
                  Crai Crypto, inim ascuns, <br />
                </p>
                <p>
                  La vecinic tron, de rou parc! <br />
                  Dar printre ei brfeau bureii <br />
                  De-o vrjitoare mntarc, <br />
                  De la fntna tinereii. <br />
                </p>
                <p>
                  i ri ghioci i toporai <br />
                  Din gropi ieeau s-l ocrasc, <br />
                  Sterp l fceau i nrva, <br />
                  C nu voia s nfloreasc. <br />
                </p>
                <p>
                  n ri de ghea urgisit, <br />
                  Pe-acelai timp tria cu el, <br />
                  Lapon mic, linitit, <br />
                  Cu piei, pre nume Enigel. <br />
                </p>
                <p>
                  De la iernat, la punat, <br />
                  n noul an, s-i duc renii, <br />
                  Prin aer ud, tot mai la sud, <br />
                  Ea poposi pe muchiul crud <br />
                  La Crypto, mirele poienii. <br />
                </p>
                <p>
                  Pe trei covoare de rcoare <br />
                  Lin adormi, torcnd verdea: <br />
                  Cnd lng sn, un rig spn, <br />
                  Cu eunucul lui btrn, <br />
                  Veni s-o-mbie, cu dulcea: <br />
                </p>
                <p>
                  - Enigel, Enigel, <br />
                  i-am adus dulcea, iac. <br />
                  Uite fragi, ie dragi, <br />
                  Ia-i i toarn-i n puiac. <br />
                </p>
                <p>
                  - Rig spn, de la sn, <br />
                  Mulumesc Dumitale. <br />
                  Eu m duc s culeg <br />
                  Fragii fragezi, mai la vale <br />
                </p>
                <p>
                  -Enigel, Enigel, <br />
                  Scade noaptea, ies lumine, <br />
                  Dac pleci s culegi, <br />
                  ncepi, rogu-te, cu mine. <br />
                </p>
                <p>
                  -Te-a culege, rig blnd... <br />
                  Zorile ncep s joace <br />
                  i eti umed i plpnd: <br />
                  Team mi-e, te frngi curnd, <br />
                  Las. - Ateapt de te coace. <br />
                </p>
                <p>
                  -S m coc, Enigel, <br />
                  Mult a vrea, dar vezi, de soare, <br />
                  Visuri sute, de mcel, <br />
                  M despart. E rou, mare, <br />
                  Pete are fel de fel; <br />
                  Las-l, uit-l, Enigel, <br />
                  n somn fraged i rcoare. <br />
                </p>
                <p>
                  - Rig Crypto, rig Crypto, <br />
                  Ca o lam de blestem <br />
                  Vorba-n inim-ai nfipt-o! <br />
                  Eu de umbr mult m tem, <br />
                </p>
                <p>
                  C dac-n iarn sunt fcut, <br />
                  i ursul alb mi-e vrul drept, <br />
                  Din umbra deas, desfcut, <br />
                  M-nchin la soarele-nelept. <br />
                </p>
                <p>
                  La lmpi de ghea, supt zpezi, <br />
                  Tot polul meu un vis viseaz. <br />
                  Greu taler scump cu margini verzi <br />
                  De aur, visu-i cerceteaz. <br />
                </p>
                <p>
                  M-nchin la soarele-nelept, <br />
                  C sufletu-i fntn-n piept, <br />
                  i roata alb mi-e stpn, <br />
                  Ce zace-n sufletul-fntn. <br />
                </p>
                <p>
                  La soare, roata se mrete; <br />
                  La umbr, numai carnea crete <br />
                  i somn e carnea, se dezumfl, <br />
                  Dar vnt i umbr iar o umfl... <br />
                </p>
                <p>
                  Frumos vorbi i subirel <br />
                  Lapona dreapt, Enigel, <br />
                  Dar timpul, vezi, nu adsta, <br />
                  Iar soarele acuma sta <br />
                  Svrlit n sus, ca un inel. <br />
                </p>
                <p>
                  - Plngi, preacuminte Enigel! <br />
                  Lui Crypto, regele-ciupearc. <br />
                  Lumina iute cum s-i plac? <br />
                  El se desface uurel <br />
                  De Enigel, <br />
                  De partea umbrei moi, s treac... <br />
                </p>
                <p>
                  Dar soarele, aprins inel, <br />
                  Se oglindi adnc n el; <br />
                  De zece ori, fr sfial, <br />
                  Se oglindi n pielea-i cheal. <br />
                </p>
                <p>
                  i sucul dulce ncrete! <br />
                  Ascunsa-i inim plesnete, <br />
                  Spre zece vii pecei de semn, <br />
                  Venin i rou untdelemn <br />
                  Mustesc din funduri de blestem; <br />
                </p>
                <p>
                  C-i greu mult soare s ndure <br />
                  Ciupearc crud de pdure, <br />
                  C sufletul nu e fntn <br />
                  Dect la om, fiar btrn, <br />
                  Iar la fptur mai firav <br />
                  Pahar e gndul, cu otrav, <br />
                </p>
                <p>
                  Ca la nebunul rig Crypto, <br />
                  Ce focul inima i-a fript-o, <br />
                  De a rmas s rtceasc <br />
                  Cu alt fa, mai criasc: <br />
                </p>
                <p>
                  Cu Laurul-Balaurul, <br />
                  S toarne-n lume aurul, <br />
                  S-l toace, gol la drum s ias, <br />
                  Cu mslaria-mireas, <br />
                  S-i ie de mprteas. <br />
                </p>
              </div>
              <div class="section">
                <span className="title">Introducere:</span>
                <p>
                  Poezia Riga Crypto i lapona Enigel face parte din volumul Joc
                  secund, publicat n anul 1930. Autorul i subintituleaz
                  poezia Balad i o include n ciclul Uvedenrode, deci n etapa
                  baladic i oriental. Este o perspectiv modern asupra
                  acestei specii literare epice, fiind de fapt un poem alegoric.
                  Asemnarea cu alt poem alegoric al literaturii romne,
                  Luceafrul de Mihai Eminescu, este evident.
                </p>

                <p>
                  Autorul nsui i intituleaz opera un Luceafr ntors.
                  Ambele poezii trateaz aceeai tem, a iubirii imposibile i a
                  incompatibilitii dintre dou fiine care aparin de lumi
                  diferite.
                </p>
                <p>
                  Caracterul alegoric al poeziei este dat de faptul c se pune
                  accentul pe problema cunoaterii: la fel ca i fa de
                  mprat, riga Crypto dorete s-i depeasc statutul de
                  fiin inferioar, dar n final constat c acest lucru este
                  imposibil.
                </p>

                <p>
                  Diferena esenial este c n cazul poeziei lui Barbu fiina
                  superioar este omul, lapona Enigel, care aspir spre o
                  cunoatere absolut. Drama este ns aceeai: cineva trebuie
                  s plteasc tentaia i tentativa de a-i depi condiia.
                  Motivele poetice care se ntlnesc sunt: transhumana,
                  tentaia Sudului, atracia pentru Soare, visul i
                  sufletul-fntn.
                </p>
                <p>
                  Un alt element comun ntre cele dou poezii este interferena
                  genurilor epice. Dei amestecarea genurilor este o trstur
                  specific romantismului, faptul c poezia modern a lui Barbu
                  are la baz o balad, explic aceast particularitate. Fiind o
                  balad, se pstreaz structura narativ deoarece poezia are
                  un fir epic i personaje.
                </p>
                <p>
                  Caracterul dramatic este oferit de dialogurile dintre
                  personaje, dar i n aceast poezie acestea au valoare de
                  simbol, fapt care subliniaz lirismul.
                </p>
              </div>
              <div class="section">
                <span className="title">Titlul:</span>
                <p>
                  Titlul baladei este construit pe ideea cuplului, dar n acest
                  caz acesta nu se ntregete datorit incompatibilitii dintre
                  cei doi eroi, care fac parte din regnuri diferite.
                </p>

                <p>
                  Fiecare se afl pe o treapt superioar n propria lume, dar
                  povestea de dragoste este imposibil mai ales datorit
                  aspiraiilor fiecruia. Poezia este structurat n dou pri,
                  fiecare dintre ele reprezentnd cte o nunt. Se mprumut
                  formula narativ specific genului epic, i anume povestirea
                  n ram.
                </p>

                <p>
                  Prima nunt este una posibil, care s-a realizat deja i are
                  rolul unui cadru pentru nunta fantastic, aceasta avnd un
                  final nedorit, riga Crypto fiind nevoit s accepte cstoria
                  cu mslria-mireas.
                </p>
              </div>
              <div class="section">
                <span className="title">Structur i compoziie:</span>

                <p>
                  {" "}
                  <strong>Prima parte</strong> cuprinde primele patru strofe
                  care au rolul de prolog al baladei i reprezint dialogul
                  menestrelului cu nuntaul frunta.. Se face trimiterea spre
                  un timp mitic, spre Evul Mediu, prin adresarea direct ctre
                  cel care va rosti povestea tragic a laponei Enigel i a lui
                  riga Crypto: Menestrel trist, Mult ndrtnic menestrel.
                  Sintagma acum o var presupune o plasare temporal, dar ea
                  nu face dect s plaseze mai mult n imprecis momentul
                  rostirii, n aceeai situaie fiind i adverbul azi.
                </p>

                <p>
                  n cea de-a doua parte, povestea este proiectat tot n
                  trecut, dar ntr-un timp nedeterminat n raport cu momentul
                  rostirii, fapt subliniat i de folosirea imperfectelor:
                  mprea, brfeau, ieeau, nu voia, tria etc.
                </p>

                <p>
                  Cntecul despre povestea tragic de iubire trebuie spus ns
                  ntr-o atmosfer prielnic, n afara cotidianului, la spartul
                  nunii, n cmar. Menestrelul are multiple modaliti de
                  interpretare:
                  <span className="citat">
                    Cu foc l-ai zis acum o var; / Azi zi-mi-l stins,
                    ncetinel,
                  </span>
                  dar trebuie s o aleag pe cea care s se potriveasc cel mai
                  bine cu atmosfera i cu povestea.
                </p>

                <p>
                  Tot aici se face o trimitere la zeul Dyonisos (zeul vinului,
                  dar i al muzicii), att prin faptul c cel care povestete
                  este un cntre, ct i prin comparaia:
                  <span className="citat">
                    mai aburit / C vinul vechi ciocnit la nunt.
                  </span>
                  Repetarea cntecului spus i n trecut sugereaz un ritual al
                  zicerii unei poveti exemplare.
                </p>

                <p>
                  <strong>A doua parte</strong> conine povestea propriu-zis
                  dintre riga Crypto i lapona Enigel i este realizat din mai
                  multe secvene poetice: portretul i descrierea mpriei
                  regelui ciupercilor (strofele 5-7), portretul i descrierea
                  mediului n care triete lapona Enigel (strofele 8 i 9),
                  ntlnirea n vis a celor doi (strofa 10), cele trei chemri
                  ale rigi i primele dou refuzuri ale laponei (strofele
                  11-15), refuzul categoric al laponei i relevarea simbolului
                  solar dup care i ghideaz existena (strofele 16-20),
                  ncheierea ntlnirii dintre cei doi (strofele 21 i 22),
                  pedepsirea lui Crypto din final (strofele 23-27).
                </p>

                <p>
                  n prima secven sunt prezentate personajele: n lumea lui,
                  Crypto este un ales, are un statut superior:
                  <br />
                  <span className="citat">
                    Des cercetat de pdurei / n pat de ru i-n hum uns, /
                    mprea peste burei / Crai Crypto, inim ascuns.
                  </span>
                  <br />
                </p>
                <p>
                  Dei admirat, are parte i de adversiti: este considerat
                  sterp i nrva de celelalte plante ale pdurii care nu
                  neleg de ce el nu i accept condiia de ciuperc i
                  ncearc s o depeasc. Fiina la care viseaz aparine de
                  regnul uman, este deosebit n lumea ei datorit faptului c
                  dorete ea o mplinire absolut reprezentat prin lumin
                  solar.
                </p>

                <p>
                  Ea penduleaz anual ntre cotidian: de la iernat i idealul
                  reprezentat de Sud, de Soare: la punat, [...] tot mai la
                  sud. Singura asemnare dintre cele dou fiine este statutul
                  superior pe care l au n propria lume, dar acest element nu
                  va fi suficient pentru mplinirea prin iubire. Deosebirile
                  sunt prezentate cu ajutorul antitezelor i a imaginilor care
                  descriu mediul existenial al celor doi.
                </p>

                <p>
                  Lumea lui Crypto se definete prin umezeal i rcoare: n
                  pat de ru i hum, n timp ce lumea laponei este un spaiu
                  rece: n ri de ghea urgisit, fapt care explic atracia
                  ei spre soare i lumin.
                </p>

                <p>
                  <strong>n drumul ei spre sud,</strong> lapona mic,
                  linitit Enigel este rugat de ctre regele ciupercilor s
                  rmn alturi de el n lumea umed i rcoroas a plantelor
                  pdurii. ntlnirea celor doi se realizeaz n lumea visului,
                  acest motiv fcnd din nou trimitere la poemul Luceafrul.
                </p>
                <p>
                  Regele ciupercilor ncearc s o ademeneasc n lumea lui prin
                  intermediul a trei chemri. Valoarea de descntec i de
                  incantaie a celor trei chemri, precum i mpletirea
                  limbajului popular cu cel literar (iac, ie dragi,
                  puiac, te frngi, somn fraged, lam de blestem),
                  subliniaz i mai mult caracterul de balad al poeziei.
                </p>

                <p>
                  <strong>n prima chemare</strong> riga Crypto o mbie pe
                  lapon cu dulcea i cu fragi, elemente definitorii
                  pentru mediul vegetal din care provine. Darul lui este refuzat
                  categoric de Enigel i exprim aspiraia ei spre Soare:
                  <br />
                  <span className="citat">
                    - Rig spn, de la sn, / Mulumesc Dumitale. / Eu m duc
                    s culeg / Fragii fragezi, mai la vale.
                  </span>
                  <br />
                </p>
                <p>
                  <strong>n a doua chemare</strong> ndrgostitul merge i mai
                  departe propunndu-i sacrificiul de sine:
                  <br />
                  <span className="citat">
                    Dac pleci s culegi, / ncepi, rogu-te cu mine..
                  </span>
                  <br />
                </p>
                <p>
                  Lapona neleapt l refuz i de data aceasta, artndu-i
                  motivele pentru care nuntirea lor este imposibil: Crypto este
                  umed i plpnd i este sftuit s atepte s se coac:
                  Team mi-e, te frngi curnd, / Las.
                </p>

                <p>
                  Ateapt de te coace. Opoziia copt / necopt se refer de
                  fapt la opoziia soare / umbr, care subliniaz de fapt
                  relaia fiecruia cu universul, incompatibilitatea peste care
                  niciunul dintre ei nu poate trece fr s se piard pe sine.
                  Crypto se teme de lumina soarelui:
                  <span className="citat">
                    de soare, / Visuri sute, de mcel, / M despart. E rou,
                    mare, / Pete are fel de fel,
                  </span>
                  n timp ce lapona aspir spre acesta:
                  <span className="citat">
                    C dac-n iarn sunt fcut, / i ursul alb mi-e vrul
                    drept, / din umbr deas, desfcut, / M-nchin la
                    soarele-nelept. .
                  </span>
                  <br />
                </p>

                <p>
                  <strong>Ultima ncercare</strong> pe care o face riga este
                  urmat de un refuz i mai categoric. Fiina superioar i d
                  seama c iubirea lor nu este posibil i c opoziia dintre
                  soare i ntuneric, dintre raiune i instinct nu va putea fi
                  niciodat anulat
                  <br />
                  <span className="citat">
                    La soare, roat se mrete; / La umbr numai carnea
                    crete.
                  </span>
                  <br />
                </p>

                <p>
                  Refuzul laponei este fatal pentru rig deoarece puterea
                  descntecului se ntoarce asupra lui, atributele lumii pe care
                  Enigel o alege se ntorc asupra lui. Dorind s-i urmeze
                  aspiraia spre fiina superioar, regele-ciupearc este
                  pedepsit pentru ndrzneala lui i transformat n ciuperc
                  otrvitoare:
                  <br />
                  <span className="citat">
                    Dar soarle, aprins inel, /<br /> Se oglindi adnc n el;
                    [...] i sucul dulce ncrete! /<br /> Ascuns-i inim
                    plesnete..
                  </span>
                  <br />
                </p>
                <p>
                  Astfel, ncercarea lui Crypto de a schimba ordinea fireasc a
                  lumii eueaz, iar ordinea este n final restabilit.
                </p>

                <p>
                  Cel ajutat{" "}
                  <span className="citat">
                    De-o vrjitoare mntarc, / De la fntna tinereii
                  </span>{" "}
                  pentru a se putea mplini, ajunge, prin oglindire (un alt
                  motiv esenial al poeziei), s se nsoeasc cu o fiin din
                  lumea lui,
                  <span className="citat">
                    Cu mslria-i mireas, / S-i ie de mprteas.
                  </span>
                </p>

                <p>
                  Opoziia dintre cele dou fiine, una uman, cealalt
                  vegetal, este subliniat i n final prin antitez fiar
                  btrn / fptur mai firav :
                  <span className="citat">
                    C sufletul nu e fntn / Dect la om, fiar btrn, /
                    Iar la fptur mai firav / Pahar e gndul, cu otrav.
                  </span>
                </p>
                <p>
                  Omul este o fiin supus greelii i ncercrii repetate
                  datorit existenei ndelungate, n timp ce creatura vegetal
                  triete un an i, prin urmare, nu are dreptul dect la o
                  ans care i este fatal.
                </p>
              </div>
            </div>
          </div>
        </Content>
      </div>
    );
  }
}
