import React, { Component } from "react";
import { Layout, Icon } from "antd";
import ExtraContent from "./ExtraContent";
// Ant design
const { Content } = Layout;

export default class Plumb extends Component {
  render() {
    return (
      <div className="content_wrapper">
        <Content
          style={{
            background: "#fff",
            padding: 24,
            marginTop: "3em",
            minHeight: 280
          }}
        >
          <div className="content">
            <div className="header">
              <h1>Plumb</h1>
              <h4>George Bacovia</h4>
            </div>

            <ExtraContent />
            <div className="body">
              <div class="section poezie">
                Dormeau adnc sicriele de plumb, i flori de plumb i funerar
                vetmnt -<br />
                Stam singur n cavou i era vnt
                <br />
                i scriau coroanele de plumb.
                <br />
                Dormea ntors amorul meu de plumb
                <br />
                Pe flori de plumb, i-am nceput s-l strig -<br />
                Stam singur lng mort i era frig
                <br />
                i-i atrnau aripile de plumb.
                <br />
              </div>
              <div class="section">
                <span className="title">Definiia curentului literar:</span>
                <p>
                  Simbolismul a aprut ca o reacie mpotriva romantismului
                  decadent i a parnasianismului. Numele curentului este propus
                  de poezia articol "Le' Symbolisme" a lui Jean Moreas, din
                  1886.
                </p>
                <p>
                  Poezia simbolist sugereaz ideile, nu le exprim, ajutndu-se
                  de valori lexicale eufonice, de culori, gnduri i sentimente.
                  Precursorul simbolismului este considerat Charles Boudelarie,
                  autorul operei "Florile rului" n care valorific estetic
                  urtului.
                </p>
              </div>
              <div class="section">
                <span className="title">Trsturi:</span>
                <p>
                  "Plumb" este o poezie simbolist datorit prezenei
                  simbolurilor, a corespondenelor, a sugestiei, a
                  muzicalitii, a laitmotivelor i a refrenelor.
                </p>

                <p>
                  Teme specifice curentului ,precum natura, iubirea i moartea
                  se regsesc n poezie. Obiectivul poeziei l constituie
                  strile sufleteti nedefinite, care neputnd fi formulate clar
                  sunt transmise pe calea sugestiei.
                </p>
              </div>
              <div class="section">
                <span className="title">Apariia:</span>
                <p>
                  Poemul a fost publicat n volumul cu acelai nume n anul 1916
                  n revista "Literatorul"
                </p>
              </div>
              <div class="section">
                <span className="title">Semnificaia titlului:</span>
                <p>
                  Titlul poeziei, " Plumb", este metaforic i simbolic sugernd
                  tema poeziei , i anume moartea. Gramatical, titlul este
                  alctuit dintr-un substantiv simplu, nearticulat, care
                  sugereaz ideea de generalitate.
                </p>
                <p>
                  n sens denotativ "plumb" semnific metal greu, n sens
                  conotativ, denot o apsare sufleteasc, monotonie, plictis i
                  spleen. Plumbul este de culoarea gri, gru' fiind culoarea
                  humei, sugernd toposul unde ajung oamenii i obiectele, o
                  stare de disperare, obsesia morii.
                </p>
                <p>
                  Ca i muzicalitate, titlul este compus din patru consoane i o
                  vocal, aceast construcie sugereaz o nchidere definit a
                  spaiului existenial, fr nici o posibilitate de ieire.
                </p>
              </div>
              <div class="section">
                <span className="title">Tem:</span>
                <p>
                  Temele poeziei sunt moartea, natura, iubirea i condiia
                  omului de geniu. Eul liric se simte singur, izolat ntr-o
                  lume, care nu l nelege i i ngrdete aspiraiile.
                </p>
              </div>
              <div class="section">
                <span className="title">Compoziia exterioar:</span>
                <p>
                  Poezia este alctuit din dou catrene, care corespund n
                  structur de adncime celor dou planuri, cel exterior din
                  prima strof, sugerat de cuvintele: sicriu, cavouri i
                  coroane.
                </p>
                <p>
                  Iar cel interior din cea de a dou strof este sugerat de
                  sentimentul de iubire, de amor.
                </p>
              </div>
              <div class="section">
                <span className="title">Structura de adncime:</span>
                <p>
                  <strong>n prima strof</strong> spaiul simbolic este nchis
                  i sufocant, el corespunde cu realitatea mediului n care
                  triete, viaa n general, odaia. Cuvintele "sicriele" i
                  "cavou", "funerar" sugereaz moartea. Metafora "flori de
                  plumb" este o sugestie a transformrii vegetalului, implicit a
                  vieii n cenuiu, n mineral. Versul "Stm singur n cavou...
                  i era vnt..." sugereaz solitudinea eului liric, pustietatea
                  lui sufleteasc.
                </p>
                <p>
                  Cuvntul "plumb" apare n strof de trei ori i se afl n
                  coresponden cu strile sufleteti ale poetului."Vntul" este
                  singurul element material dinamic, iar "" reprezint sunetul
                  repetat obsesiv, asimilat ca i litera care uier. n strof
                  regsim motive precum cel al somnului, al izolrii i al
                  decorului funebru.
                </p>
                <p>
                  <strong>n cea de a dou strof</strong> spaiul este nchis,
                  interior, este un spaiu poetic interiorizat. Sentimentul de
                  iubire este sugerat de "amor ntors" care semnific moartea
                  sentimentului, inaccesibilitatea lui. Iubirea nu este
                  nltoare, ci rece, nu exist perspective de mplinire.
                </p>
                <p>
                  Poezia reprezint o confesiune liric n care se folosete
                  persoan nti singular. Mesajul transmis este concis i
                  cenzurat de orice fel de sentimentalisme. n poezie alterneaz
                  simbolul "plumb" cu alte cuvinte, crora le schimb sensul,
                  astfel "amor de plumb" sugereaz un sentiment apstor,
                  oboseal psihic.
                </p>

                <p>
                  Imperfectul verbelor "stm, "dormea", "era", sugereaz lipsa
                  oricror stri optimiste, aciunea lor neavnd finalitate.
                  Gramatica cuvintelor este sugerat de elemente funerare,
                  precum culorile gri i negru, prezente n poezie. George
                  Bacovia nsui a mrturisit c "n poezie m-a obsedat
                  ntotdeauna un subiect de culoare, pictura cuvintelor sau
                  audiia colorat. Fiecrui sentiment i corespunde o culoare."
                </p>
                <p>
                  Toate cuvintele din poezie au o sonoritate surd. n versul
                  "i-am nceput s-l strig", strigtul este accentuat prin
                  acest vers, care sugereaz senzaii intense, viziunile eului
                  liric asupra vieii. Eul liric dorete s evadeze din aceast
                  izolare, dar nu poate i rmne doar un strigt neajutorat, de
                  disperare, a expresiei a suferinei.
                </p>
                <p>
                  Totul la George Bacovia trimite spre imposibilitatea
                  comunicrii. Plumbul apare simetric n ambele strofe, de ase
                  ori, apare ca i rim i sugereaz neputin eului liric de a
                  evada din aceast lume sufocant.
                </p>
                <p>
                  Atmosfera acestei poezii este tulburtoare, mai nti prin
                  simpla evocare a unei realiti, moarte, la nivelul semantic
                  al cuvintelor folosite: sicrie, cavou, coroanele care
                  scriau i prin repetiia cuvntului "plumb", cuvnt cheie
                  al ntregii poezii, cuvnt magic folosit obsesiv. emoia
                  poeziei se declaneaz nc din primul vers al celei de a doua
                  strof : "Dormea ntors amorul meu de plumb".
                </p>
                <p>
                  Cuvntul "ntors" realiznd misterul poeziei. Poetul sugereaz
                  o cdere, un zbor n jos, care are specialitatea morii.
                  Aripile fiind de plumb, cderea este surd i grea iar eul
                  liric nu mai are posibilitatea nlrii. Muzicalitatea este
                  realizat de tonalitatea minor a imperfectelor : stm,
                  dormeam, era i de cuvinte cu valoare de onomatopee i
                  stridente scriau i frig.
                </p>
              </div>
            </div>
          </div>
        </Content>
      </div>
    );
  }
}
