import React, { Component } from "react";
import { Layout } from "antd";
import ExtraContent from "./ExtraContent";
// Ant design
const { Content } = Layout;

export default class AlexandruLapusneanu extends Component {
  render() {
    return (
      <div className="content_wrapper">
        <Content
          style={{
            background: "#fff",
            padding: 24,
            marginTop: "3em",
            minHeight: 280
          }}
        >
          <div className="content">
            <div className="header">
              <h1>Alexandru Lpusneanu</h1>
              <h4>Costache Negruzzi</h4>
            </div>

            <ExtraContent />
            <div className="body">
              <div class="section">
                <span className="title">Definiia nuvelei istorice:</span>
                <p>
                  Specie a genului epic n proz cu dimensiuni mai mari n
                  comparaie cu cele ale schiei i povestirii,dar mai reduse
                  dect ale romanului,avend un singur fir epic,conflict puternic
                  i personaje reliefate.Nuvela este istoric doarece se evoc o
                  imagine a trecutului(cea a lui Lpuneanu), autorul cerceteaz
                  istoria Moldovei n vederea realizrii nuvelei.
                </p>
              </div>
              <div class="section">
                <span className="title">Trsturi:</span>
                <p>
                  Opera lui Negreuzzi este c specie o nuvel istoric deoarece
                  naraiunea are un caracter obiectiv fr intervenii
                  subiective din partea naratorului, nararea se face la persoana
                  a treia naratorul fiind sobru,omniscient, omniprezent, detaat
                  i predominant obiectiv.
                </p>

                <p>
                  Faptele prezentate sunt verosimile: autorul povestete n fond
                  istoria Moldovei fcnd anumite schimbri i adaugant elemente
                  imaginative. Are un numr restrns de personaje: Alexandru
                  Lpuneanu, Ruxandra, Motoc, Spancioc, Stroici etc.
                </p>
              </div>
              <div class="section">
                <span className="title">Apariie:</span>
                <p>
                  Nuvela istoric "Alexandru Lpuneanu" a aprut n anul 1840
                  i a fost publicat la Iai n revista "Dacia literar".
                </p>
              </div>
              <div class="section">
                <span className="title">Titlul:</span>
                <p>Titlul este omonim cu personajul principal al nuvelei.</p>
              </div>
              <div class="section">
                <span className="title">Tema:</span>
                <p>
                  Tema nuvelei este dat de evocarea unei domnii zbuciumate din
                  medievalitatea romneasc i anume ntoarcerea lui Lpuneanu
                  pentru cea de-a doua domnie (1564-1569).
                </p>
              </div>
              <div class="section">
                <span className="title">Viziunea despre lume a autorului:</span>
                <p>
                  n ceea ce privete viziunea despre lume a autorului, nuvela
                  de inspiraie istoric aduce momentul apariiei nu un model de
                  patriotism,ci un antimodel de conductor, ca un avertisment
                  adresat contemporanilor ntr-o perioad de efervescen
                  revoluionar.
                </p>
              </div>

              <div class="section">
                <span className="title">Perspectiva narativ:</span>
                <p>
                  Nararea se face la persoana a treia,naratorul fiind
                  omniscient,omniprezent.
                </p>
              </div>

              <div class="section">
                <span className="title">Compoziia operei:</span>
                <p>
                  Nuvela este clasic, concis i sobr avnd 4 capitole fiecare
                  purtant cte un moto reprezentativ, care sintetizeaz
                  coninutul.
                </p>
                <ol type="I">
                  <li>
                    <strong>"Dac voi nu m vrei,eu v vreu!"</strong>{" "}
                    reprezint rspunsul dat de lpuneanu soiei.
                  </li>

                  <li>
                    <strong>"Ai s dai sam,doamna!"</strong> reprezint
                    cuvintele jupnesei ai crei so fusese ucis de porunca
                    domnitorului. Cuvintele vor deveni obsesia doamnei Ruxandra
                    care i va cere soului s nceteze cu omorurile.
                  </li>

                  <li>
                    <strong>"Capul lui Mooc vrem!" </strong>sunt cuvintele
                    mulimii adunate n grab n curile palatului domnesc.
                  </li>

                  <li>
                    <strong>
                      "De m voi scula,pre muli am s popesc i eu!"
                    </strong>{" "}
                    sunt cuvintele lui Lpuneanu aflat ntr-un moment de
                    luciditate,trezit din agonie i amenin pe clugrii care
                    l-au popit.
                  </li>
                </ol>
              </div>

              <div class="section">
                <span className="title">Conflictele:</span>

                <p>
                  Conflictele sunt complexe i pun n lumin personalitatea
                  puternic a personajului principal.
                </p>
                <p>
                  Conflictul principal este de natur exterioar, de ordin
                  politic i prezint lupta pentru putere dintre domnitor i
                  boieri.Cel secundar se d ntre domnitor i Motoc,
                  particulariznd dorina de rzbunare a domnitorului.
                </p>
                <p>
                  Conflictul social dintre boieri i popor, limitat ns la
                  revolta mulimii din capitolul 3.
                </p>
              </div>

              <div class="section">
                <span className="title">Relaii spaiale i temporale:</span>
                <p>
                  Timpul i spaiul aciunii sunt precizate i confer
                  verosimilitate naraiunii,ntoarcerea lui Lpuneanu pentru
                  cea de-a doua domnie (1564-1569).
                </p>

                <p>
                  n primele trei capitole evenimentele se desfoar ndat
                  dup revenirea acestuia la tron,iar n ultimul capitol se
                  trece direct la moarea lui, 4 ani mai trziu. Evenimentele se
                  desfoar n cetatea hotinului, n curtea domneasc i n
                  palatul domnitorului.
                </p>
              </div>

              <div class="section">
                <span className="title">Povestirea:</span>
                <p>
                  <strong>I "Dac voi nu m vrei, eu v vreau"</strong> Dup ce
                  Iacob Euclid, poreclit Despotul, murise de mna lui tefan
                  Toma, iar dup ce Alexandru Lpuneanu fusese nvins de dou
                  ori, se retrsese la Constantinopol.
                </p>
                <p>
                  El pleac spre Moldavia cu gndul de a-i recpta tronul cu
                  ajutorul celor 7 mii de spahii i celor 2 voinici Bogdan i
                  Mooc, postelnicului Veveri, i sptarului Stroici. Ajuns n
                  Moldova, Lpuneanu afl c nu este dorit nici de popor nici
                  de boieri. Printre aceti boieri se afl i Mooc cel care-l
                  vnduse o dat pe Lpuneanu, cum l-a vndut i pe Iacov
                  Eraclid, dup ce aflase de la Alexandru Lpuneanu c va domni
                  peste Moldova cu sau fr acordul boierilor sau al poporului.
                </p>
                <p>
                  Mooc ncearc s-l fac s aib din nou ncredere n el, dar
                  subtil Lpuneanu tindu-i vorbele mieroase cu zictoarea:
                  "Lupul prul schimb dar nravul ba" Lpunuanu recunoate c
                  nu-i va pta sabia cu sngele lui Mooc, care sub orice form
                  ncearc s-l ademeneasc pe Lpuneanu s aib ncredere n
                  el.
                </p>
                <p>
                  <strong>II "Ai s dai sam doamna"</strong> Toma nefiind
                  pregtit s se nfrunte cu Alexandru fuge n Valahia astfel
                  nct Lpuneanu i recpt uor tronul. Devenind domnitor,
                  poporul spera ntr-o via mai bun cu el n timp ce boierii
                  sunt ngrozii. Dar acesta dezamgete poporul dnd foc la
                  toate cetile Moldovei nafar de Hotin.
                </p>
                <p>
                  Domnitorul lu toate averile boierilor sub tot felul de
                  pretexte, fr ca acetia s se mpotriveasc sau s mcar s
                  comploteze mpotriva lui cci tiau c vor fi omori aa cum
                  au fost omori i ali boieri Alexandru cunoscan-do pe fiica
                  lui Petru Rare, Ruxandra se cstorete cu ea, cstoria
                  fiind fcut cu ajutorul intereselor comune a celor doi soi
                  Zilele treceau astfel ncintr-o zi pe Ruxandra o opri n
                  strad o femeie care i spuse c e vduv c are 5 copii i c
                  soul ei ucide fr mil, i pentru faptele rele fcute de
                  soul ei Ruxandra va plti.
                </p>
                <p>
                  Aceasta se sperie i l roag pe Lpuneanu s nu mai ucid pe
                  nimeni. Acesta c drept leac pentru tristeea ei d un mare
                  osp la palat la care erau invitai toi boierii.
                </p>
                <p>
                  <strong>III "Capul lui Motoc vrem"</strong> Dupa ce fusesera
                  anuntati toti boierii de marele ospat ce va fii dat, ei se
                  dusera la mitropolie unde veni si Lpusneanu pentru a asculta
                  slujba dupa care, Lapusneanu cobora si pupa moastele sfantului
                  Ioan In mitropolie el cuvanta in fata boierilor si a poporului
                  spunand ca este timpul s termine cu toate crimele si c de
                  acum inainte s se aiba cu boierii ca fratii.
                </p>
                <p>
                  Acesta este iertat de boieri si de popor, mai putin de boieri
                  : Spancioc si Stroici acestia nefiind luati in seama. Dup
                  acel discurs boierii incalecara pe cai si pornira spre palat.
                  In interiorul palatului o masa ca-n povesti astepta Dupa ce se
                  ospatar Lpusneanu il lovi in cap cu Buzduganul pe boierul
                  Veverita in timp ce acesta se inchin in fata lui.
                </p>
                <p>
                  Dupa aceasta incepe un adevarat razboi intre osteni si boieri
                  ramanand dupa batalie o baie de sange Poporul, stand la poarta
                  palatului striga in gura mare "Capul lui motoc vrem",
                  facandu-l pe acesta vinovat pentru cele intamplate.
                </p>
                <p>
                  Domnitorul dadu norodului ceea ce cereau sub forma de cadou de
                  la domnitor asezand capetele boierilor sub forma de piramida
                  dupa rangul fiecaruia Lapusneanu o chema pe Ruxandra care
                  ingrozita lesina. IV"De ma voi scula, pe multi am sa popesc si
                  eu" Dupa 4 ani de la acel macel, pe tiran il apuca dorul de a
                  ucide asa ca incepu a ucide din oamenii norodului, peste care
                  pusese stapanire.
                </p>
                <p>
                  Dar il chinuia gandul ca nu a petut sa-i omoare pe cei doi
                  boieri:Stroici si Spancioc, pentru asta trebuia sa-i
                  supravegheze, astfel incat se muta in cetatea Hotinului unde
                  se inbolnaveste. Fiind pe patul mortii acesta ceru sa fie
                  popit inainte sa moar, dupa care lesina. Dupa un timp
                  indelungat isi reveni si ceru ca Ruxandra si fiul ei sa fie
                  omorat, cu toate ca avea mustrari de constiinta.
                </p>
                <p>
                  Moartea lui Lpusneanu pusa la cale de cei 2 boieri care-l
                  otravesc. Trupul sau a fost dus la manastirea Slatina.
                </p>

                <p>
                  <strong>
                    IV"De m voi scula, pe muli am s popesc i eu"
                  </strong>
                  Dup 4 ani de la acel mcel, pe tiran l apuc dorul de a
                  ucide aa c ncepu a ucide din oamenii norodului, peste care
                  pusese stpnire. Dar l chinuia gndul c nu a petut s-i
                  omoare pe cei doi boieri:Stroici i Spancioc, pentru asta
                  trebuia s-i supravegheze, astfel nct se mut n cetatea
                  Hotinului unde se nbolnvete.
                </p>

                <p>
                  Fiind pe patul morii acesta ceru s fie popit nainte s
                  moar, dup care lein. Dup un timp ndelungat i reveni i
                  ceru ca Ruxandra i fiul ei s fie omort, cu toate c avea
                  mustrri de contiin. Moartea lui Lpuneanu pus la cale de
                  cei 2 boieri care-l otrvesc. Trupul su a fost dus la
                  mnstirea Slatina.
                </p>
              </div>

              <div class="section">
                <span className="title">Caracterizarea personajelor:</span>
                <p>
                  <strong> Alexandru Lpuneanu:</strong> este personajul
                  principal al nuvelei cu acelai nume de Costache Negruzzi.
                  Nuvela apare pentru prima dat n primul numr al revistei
                  ieene Dacia literar n 1840 sub titlul de Scene istorice
                  din cronicele Moldaviei. Ca surs de inspiraie, autorul
                  folosete Letopiseul rii Moldovei de Grigore Ureche.
                </p>
                <p>
                  Alexandru Lpuneanu este un personaj cu rdcin atestat
                  istoric, individualitate proprie, complex, personaj pozitiv i
                  totodat negativ. Dup criteriul social i uman, e tipul
                  domnitorului tiran.
                </p>
                <p>
                  Dup structura afectiv-intelectual i de caracter are un
                  temperament mobil, sensibilitate nestpnit. Viziunea asupra
                  realitii e pragmatic i realist. n capitolul I, Negruzzi
                  utilizeaz dialogul ca procedeu de caracterizare.
                </p>
                <p>
                  Dialogul se bazeaz pe replici i are scopul de a nviora
                  aciunea, dar i de a-i da un dramatism uneori. Astfel,
                  personajul i relev propriile trsturi prin intermediul
                  gesturilor i al gndurilor. Lpuneanu vorbete n pilde,
                  folosindu-se de exclamaii i ntrebri retorice.
                </p>
                <p>
                  El tie cum s ndrepte ascuitul discuiei n orice situaie.
                  Sintagma Dac voi nu m vrei, eu v vreu, i dac voi nu m
                  iubii, eu v iubesc pre voi, i voi merge ori cu voia, ori
                  fr voia voastr denot dorina i voina de a domni,
                  ncptnare, ambiie, sigurant i horrea domnitorului.
                </p>
                <p>
                  Dac n primul capitol caracterizarea i portretul lui
                  Lpuneanu este scos n evident prin intermediul antitezei
                  dintre boieri i Lpuneanu, n al doilea capitol,
                  caracterizarea lui reiese din antitez romantic i totodat
                  conflictual dintre domnia Ruxanda i el.
                </p>
                <p>
                  Domnia Ruxanda e plin de nelepciune, gingsie, pe cnd
                  Lpuneanu e crud, nemilos i ftarnic. Cerina domniei de a
                  nceta omorurile l nfurie, punnd mna pe jugher. Este o
                  reacie necontrolat a lui, nepotrivit pentru un domnitor.
                </p>
                <p>
                  Prin aceast aciune, Lpuneanu dovedete a fi un om cu o
                  fire impulsiv, obinuit s judece i s fac dreptate singur,
                  dup propriile reguli. Auzind cererea domniei, Lpuneanu o
                  mustr pentru vorbele nebune, dar promite n final c va
                  nceta cu omorurile, ns nu nainte de a-i da leac de fric.
                  n momentul de fa, Lpuneanu ncearc s fie calm i se
                  poart autoritar fat de soia sa, ns i cinic pentru c n
                  sine leacul promis o va ngrozi.
                </p>
                <p>
                  Faptele lui sunt impresionante prin cruzime i rzbunare:
                  osptul unde sunt masacrai 47 de boieri. Mooc e dat pe
                  minile mulimii, ce-l sfie de viu pentru ca n final s fie
                  asasinat. Lpuneanu surprinde caracteristicile boierilor.
                </p>
                <p>
                  Capacitatea de a-i stpni impulsurile violente l ajut s
                  ctige n orice situaie. Este expert n manipulare, ns
                  poate fi i tandru
                  <span className="citat">
                     o ridic ca pe o pn i o pune pe genunchii si
                  </span>
                  Reiese c e vorba de un conflict interior.
                </p>
                <p>
                  Alexandru Lpuneanu nu concepe s triasc fr putere
                  <span className="citat">
                    "Eu nu sunt clugr. Sunt domn! Sunt Alexandru-vod".
                  </span>
                  ncepe s-i amenine pe cei din jur, n momentul n care se
                  trezete n letargie i observ c este clugr
                  <span className="citat">
                    M-ai popit voi, dar de m voi scula pe muli am s popesc
                    i eu
                  </span>
                  , i ajunge s-i amenine propriul fiul cnd afl c acesta
                  este domn.
                </p>
                <p>
                  Alexandru Lpuneanu rmne un personaj bine individualizat,
                  cu lumini i umbre, cu gesturi i cuvinte memorabile conturnd
                  personalitatea domnului tiran.
                </p>
                <p>
                  <strong>Doamna Ruxanda:</strong> Fiind o nuvel, oper
                  literar Alexandru Lpuneanul  are un numr relativ redus
                  de personaje fa de roman, dar mai multe dect schi, iar
                  accentual cade pe caracterizarea personajului principal.
                </p>
                <p>
                  Cu toate acestea o prezen remarcabil o face i Doamna
                  Ruxanda, soia personajului principal. Ea apare ca personaj
                  secundar i este fiica, sor i soie de domn, simboliznd
                  frumuseea, buntatea, iertarea i nelegerea.
                </p>
                <p>
                  Direct, prin descriere, autorul i evideniaz mai nti
                  vestimentaia aleas i frumuseea ei vestit, realizndu-i
                  portretul fizic : Peste zobonul de stof aurit, purta un
                  beniel de felendras albastru, blnit cu samur, a crei mnece
                  atrnau dinapoi ; era nchis cu un colan de aur, ce se
                  ncheia cu mari paftalme de matostat, mpregiurate cu petre
                  scumpe, iar pe gurmanjii ei atrn o salb cu multe iruri de
                  mrgritar.
                </p>
                <p>
                  Silicul de samur, pus cam ntr-o parte, era mpdobit cu un
                  surguci alb i sprijinit cu o floare mare de smaragde. Prul
                  ei, dup moda de atuncea se mpreaa despletit pr umerii i
                  pe spatele sale.
                </p>
                <p>
                  Figura ei avea acea frumusee, care fcea odinioar vestite pe
                  femeile din Romnia. mbrcminta i inuta ei degaj
                  somptuozitate, fiind la nlimea rangului su domnesc.
                  Iubitoare i supus i apreciaz soul cruia i adreseaz
                  cuvinte pline de afeciune i respect :  bunul meu domn , 
                  viteazul meu so ,  maria ta este puternic .Fire sensibil,
                  nu suport violentele i este hotrt s mpiedice pe
                  Lpuneanul de la noi crime, aducndu-i diverse argumente : 
                  Ce-i lipsete mriei tale ? N-ai cu nime rzboi ; ara e
                  linitit i supus.
                </p>
                <p>
                  Judeca c dup via este i moarte, i c mria-ta eti
                  muritori ai s dai sama !  ndrjit n hotrrea ei, nu
                  cedeaz nici atunci cnd voievodul se mnie i devine
                  impulsive, i insist cu orice risc, exprimndu-i energic
                  dorina.
                </p>
                <p>
                  Principalul procedeu de caracterizare a ei, folosit de autor,
                  este antiteza cu Lpuneanul. Dac acesta este dur, titanic,
                  crud, ipocrit ,impulsive, doamna Ruxanda este o fire angelic,
                  suav, delicat, sensibil, sincer i supus. Antitezei i se
                  adaug caracterizarea direct prin descriere, cu ajutorul
                  creia autorul i realizeaz portretul fizic, insistnd asupra
                  vestimentaiei i frumuseii.
                </p>

                <p>
                  Domnia Ruxanda este raportat, totodat, i la celelalte
                  personaje, n afar de Lpuneanul. Ea reacioneaz, astfel,
                  n funcie de ameninarea jupnesei cu cinci copii i a
                  ndemnurilor lui Spancioc i ale mitropolitului Teofan.
                </p>

                <p>
                  Tot indirect sunt evideniate nsuirilesale prin intermediul
                  dialogului, care-i relev i strile sufleteti i atitudinea
                  n funcie de mprejurri i de interlocutor.
                </p>
              </div>
            </div>
          </div>
        </Content>
      </div>
    );
  }
}
