import React, { Component } from "react";
import { Layout, Icon } from "antd";
// Ant design
const { Content } = Layout;

export default class AciSosiPeVremuri extends Component {
  render() {
    return (
      <div className="content_wrapper">
        <Content
          style={{
            background: "#fff",
            padding: 24,
            marginTop: "3em",
            minHeight: 280
          }}
        >
          <div className="content">
            <div className="header">
              <h1>Aci sosi pe vremuri</h1>
              <h4>Ion Pillat</h4>
            </div>

            <div className="extra">
              <button>
                <Icon type="sound" />
                <span>Audio</span>
              </button>

              <button>
                <Icon type="video-camera" />
                <span>Video</span>
              </button>
            </div>
            <div className="body">
              <div class="section poezie">
                La casa amintirii cu-obloane i pridvor, <br />
                Pienjeni zbrelir i poart, i zvor. <br />
                Iar hornul nu mai trage alene din ciubuc <br />
                De cnd luptar-n codru i poteri, i haiduc. <br />
                n drumul lor spre zare mbtrnir plopii. <br />
                Aci sosi pe vremuri bunica-mi Calyopi. <br />
                Nerbdator bunicul pndise de la scar <br />
                Berlina legnat prin lanuri de secar. <br />
                Pe-atunci nu erau trenuri ca azi, i din berlin <br />
                Sri, subire, -o fat n larg crinolin. <br />
                Privind cu ea sub lun cmpia ca un lac, <br />
                Bunicul meu, desigur, i-a recitat Le lac. <br />
                Iar cnd deasupra casei ca umbre berze cad, <br />
                i spuse Sburtorul de-un tnr Eliad. <br />
                Ea-l asculta tcut, cu ochi de peruzea... <br />
                i totul ce romantic, ca-n basme, se urzea. <br />
                i cum edeau... departe, un clopot a sunat, <br />
                De nunt sau de moarte, n turnul vechi din sat. <br />
                Dar ei, n clipa asta simeau c-o s rmn... <br />
                De mult e mort bunicul, bunica e btrn... <br />
                Ce straniu lucru: vremea! Deodat, pe perete <br />
                Te vezi aievea numai n tersele portrete. <br />
                Te recunoti n ele, dar nu i-n faa ta, <br />
                Cci trupul tu te uit, dar tu nu-l poi uita... <br />
                Ca ieri sosi bunica... i vii acuma tu: <br />
                Pe urmele berlinei, trsura ta sttu. <br />
                Acelai drum te-aduse prin lanul de secar. <br />
                Ca dnsa tragi, n dreptul pridvorului, la scar. <br />
                Subire, calci nisipul pe care ea sri. <br />
                Cu berzele ntr-nsul amurgul se opri... <br />
                i m-ai gsit, zmbndu-mi, c prea naiv eram <br />
                Cnd i-am optit poeme de bunul Francis Jammes. <br />
                Iar cnd n noapte cmpul fu lac ntins sub lun <br />
                i-am spus Balada lunei de Horia Furtun, <br />
                M-ai ascultat pe gnduri, cu ochi de ametist, <br />
                i i-am prut romantic i poate simbolist. <br />
                i cum edeam... departe, un clopot a sunat <br />
                . Acelai clopot poate . n turnul vechi din sat... <br />
                De nunt sau de moarte, n turnul vechi din sat. <br />
              </div>
              <div class="section">
                <span className="title">Definiia traditionalismului:</span>
                <p>
                  Denumirea curentului sugereaza faptul ca acest curent cultural
                  pretuieste, apara si promoveaza traditia, perceputa ca o
                  insumare a valorilor arhaice, traditionale ale spiritualitatii
                  expunse in permanenta pericolului degradarii si eroziunii.
                </p>
              </div>
              <div class="section">
                <span className="title">Aparenta la curentul literar:</span>
                <p>
                  "Aci sosi pe vremuri" se incadreaza in curentul traditionalist
                  deoarece se poate remarca prezenta bisericii, a idilizari
                  trecutului prin aducerea frecventa in prim plan a unor imagini
                  ale spatiuliu natal patriarhal (aflat la mosia argeseana
                  Florica), precum si de natura campeneasca in care prind viata
                  amintirile copilariei, casa parinteasca, interiorul si
                  imprejurimile casei.
                </p>
              </div>
              <div class="section">
                <span className="title">Apariia:</span>
                <p>
                  Poezia a fost inclusa in ciclul "Trecutul viu", care face
                  parte din volumul "Pe Arges in sus ", aparut in anul 1923,
                  deci interbelic. Volumul descrie spatiul copilariei poetului
                  (mosia Florica), casa parinteasca si natura campeneasca
                  insufletita de amintirile de altadata.
                </p>
              </div>
              <div class="section">
                <span className="title">Tem:</span>
                <p>
                  Tematica rurala isi subsumeaza in poezia lui Pillat doua teme
                  de circulatie universala, anume "iubirea" si "timpul".
                  Universul rural este ales ca topos al concretizarii iubirii,
                  care se manifesta atat in planul trecutului, al timpului
                  recuperat prin amintire, cat si in cel al prezentului, al
                  trairii celui care nu face altceva decat sa repete experienta
                  celor dinaintea lui. Astfel ca, poezia devine o meditatie
                  nostalgica pe tema trecerii ireversibile a timpului, tema
                  asociata cu repetabilitatea destinului uman, a ciclicitatii
                  vietii.
                </p>
                <p>
                  Aceasta surgere neincetata a timpului ii provoaca eului liric
                  o enorma suferinta, din perspectiva careia, autorul va descrie
                  spatiul rural pe fundalul caruia va proiecta criza launtrica
                  provocata fie de aceasta trecere ireversibila a timpului, fie
                  de instrainarea fata de satul natal, argument ce indreptateste
                  afirmatia ca Pillat este poet traditionalist doar ptin
                  recuzita, dar modern prin viziune.
                </p>
              </div>
              <div class="section">
                <span className="title">Semnificatia titului:</span>
                <p>
                  Titlul este metaforic si simbolic. Sintagma titlului contine
                  doua repere deictice (de spatiu ,adverbul de loc cu forma
                  regionala "aci" si de timp,locutiunea adverbiala "pe vremuri")
                  precum si forma verbala de perfect simplu "sosi" care
                  desemneaza o actiune recent incheiata si asumata de poet.
                </p>
                <p>
                  Interpretat in corelatie cu intregul text, cel dintai element
                  deictic, spatiul lui "aci" anuntat de titlu, desemneaza "casa
                  amintirii", o casa devenita posesiune sufleteasca, proprietate
                  aflata in stapanirea memoriei. Din acest punct de vedere,
                  reperul spatial devine elementul stabilsustras devenirii, un
                  topos ce isi subordoneaza dimensiunea temporala unui prezent
                  etern. Cel de-al doilea element, "vremurile" contine acceasi
                  sugestie a trecerii prezente care eternizeaza in amintire.
                </p>
                <p>
                  Daca locul cu lanurile sale de secara si umbrele berzelor
                  neschimbate in rotirea anotimpurilor pare a fi
                  acelasi,vremurile dau impresia ca sunt mereu altele datorita
                  ciclicitatii prezentelor umane, care formeaza noi cupluri.
                  Toate aceste elemente morfologice sugereaza ideea ca
                  experientele vietii omenesti sunt reluate de generatiile care
                  se succed. Asadar, stabilirea cadrului in care iubirea iubirea
                  idilizata va fi evocata din dubla perspectiva a trecutului si
                  a prezentului, face ca perspectiva poetica sa se circumscrie
                  unui timp si spatiu ca de basm, mitic, nedefinit.
                </p>
              </div>
              <div class="section">
                <span className="title">Compoziia exterioar:</span>
                <p>
                  Din punct de vedere compozitional, poetul nu renunta la
                  rigorile prozodiei, chiar daca opera lui se afla in confluenta
                  dintre traditionalism si modernism. Pillat alcatuieste textul
                  poeziei din 19 distishuri si un vers final, liber, un monostih
                  (care indeplineste rolul de lait motiv al poeziei), pastrand
                  ritmul (iambic), rima (imperecheata) si masura (de 13-14
                  silabe), conferindu-i astfel poeziei traditionaliste o
                  structura de tip clasic
                </p>
                <p>
                  Comunicarea poetica se realizeaza atat in registrul lirismului
                  obiectiv (cu elemente de narativitate simbolica si meditatie
                  cu caracter general-uman) cat si a lirismului subiectiv
                  (marcat de prezenta eului liric, comunicarea directa a
                  trairilor si a sentimentelor la persoana I singular,
                  sentimentul elegiac, meditativ), subiectivitatea fiind
                  sustinuta de prezenta marcilor lexico-gramaticale specifice
                  (verbe la persoana I si a II-a singular, pronume personale,
                  adjective posesive) si elemente deictice spatio-temporale
                  "aci"-"accolo", "acum"-"atunci".
                </p>
              </div>
              <div class="section">
                <span className="title">Structura de profunzime:</span>
                <p>
                  Se organizeaza in 3 mari secvente lirice : <br />
                  <br />
                  <strong>Secventa a I-a: </strong>
                  cuprinde primele 3 distihuri (6 versuri) in care se regasesc
                  indici spatiali ai unui cadru rustic si intim, precum si
                  indici temporali ce compun campul semantic al "amintirii". In
                  opozitie cu timpul perceput la nivel individual, timpul
                  universal, macrotimpul, se scurge dupa alte dimensiuni,
                  dandu-i omului impresia de incremenire. Privita din
                  perspectiva fiintei umane trecatoare, axa temporala cosmica ii
                  trezeste eului liric un peternic sentiment de neputinta si
                  nostalgie, in timp ce trecutul este vazut ca un timp al
                  vitalitatii, idee sugerata prin aluzia la "haiduci" si la
                  "poteri".
                </p>
                <br />
                <p>
                  <strong> Secventa a II-a: </strong>
                  contine urmatoarele sapte distihuri (14versuri) in care se
                  reconstituie scenariul erotic de altadata, ai carui
                  protagonisti sunt bunicii. Prin recuzita bunicii
                  ("berlina","criolina") si prin aluziile culturale la cele doua
                  poeme romantice recitate de bunic este sugerat trecutul.
                  Dincolo de aceste elemente, se insista asupra portretului
                  bunicii, dominat de motivul ochiilor "de peruzea", ca simbol
                  al privirii protectoare a femeii, pentru ca apoi imaginea
                  auditiva neasteptata introdusa in text sa sporeasca doza de
                  ambiguitate ("Si cum sedeau...departe, un clopot a sunat...").
                  Ambivalenta spatiului sacru, care celebreaza unirea celor doi,
                  reaminteste de conditia de muritor a individului, pe care
                  poetul incearca sa o compenseze cu iluzia eternizarii clipei
                  de dragoste.
                </p>
                <p>
                  <strong>Secventa a III-a: </strong>
                  contine opt distihuri (16 versuri) si debuteaza prin
                  realizarea legaturii dintre scenariul erotic trecut si cel
                  prezent. Motivul fotografii exprima drama trecerii timpului,
                  acest intermezzo dand o tonalitate grava poemului, astfel
                  incat pe fundalul scenariului erotic este proiectata drama
                  instrainarii de timpul copilariei, dar si cea a efemeritatii
                  fiintei umane. Povestea de dragoste din prezent ii are ca
                  protagonisti pe nepoti care vor repeta gesturile buniciilor
                  peste timp. Legatura dintre generatii se realizeaza cu
                  ajutorul unui aparent nesemnificativ detaliu al ochiilor,
                  amanunt care devine unul spiritualizatin momentul in care
                  portretul fizic al iubitei se intregeste. Diferentele dintre
                  cupluri tin de moda vremii, insa cadrul pastreaza acelasi
                  fundalin care se schimba doar cateva elemente de decor. Ca
                  intr-un ritual, epilogul cuprinde ultimele trei versuri in
                  care se reia una din imaginile auditive din cea de a doua
                  secventa, respectiv bataia clopotului, care pastreaza aceeasi
                  nota de ambiguitate, nunta semnificand consfintirea ritualului
                  erotic, iar moartea asumarea conditiei umane.
                </p>
                <div class="section">
                  <span className="title">
                    Elemente de opozitie si simetrie:
                  </span>
                  <p>
                    Cele 2 planuri ale poeziei, trecutul si prezentul
                    (distihurile 3-9) si prezentul (distihurile 12-19) sunt
                    redate succesiv prin utilizarea elementelor de simetrie si
                    opozitie a planurilor, construite pe relatia "acum" -
                    "atunci", rolul lor fiind acela de a accentua ideea de
                    ciclicitate a vietii si a iubirii. Povestea de dragoste a
                    celor 2 cupluri are o multime de puncte comune, unul fiind
                    chiar "luna" care devine martora iubirii dintre oameni, si
                    implicit, a ideilor buniciilor si nepotiilor. Un alt element
                    comun al peisajului care gazduieste cele 2 povesti este
                    "lanul de secara" prezent atat pe vremea bunicilor, cat si
                    pe vremea nepotilor. Recitarile apartin atat bunicului, cat
                    si nepotului acestuia, in incercarea celor doi de a-si
                    cuceri iubita.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </Content>
      </div>
    );
  }
}
