import React, { Component } from "react";
import { Layout } from "antd";
import ExtraContent from "./ExtraContent";
// Ant design
const { Content } = Layout;

export default class FloriDeMucigai extends Component {
  render() {
    return (
      <div className="content_wrapper">
        <Content
          style={{
            background: "#fff",
            padding: 24,
            marginTop: "3em",
            minHeight: 280
          }}
        >
          <div className="content">
            <div className="header">
              <h1>Flori de mucigai</h1>
              <h4>Tudor Arghezii</h4>
            </div>

            <ExtraContent />
            <div className="body">
              <div class="section poezie">
                Le-am scris cu unghia pe tencuial <br />
                Pe un prete de firid goal, <br />
                Pe ntuneric, n singurtate, <br />
                Cu puterile neajutate <br />
                Nici de taurul, nici de leul, nici de vulturul <br />
                Care au lucrat mprejurul <br />
                Lui Luca, lui Marcu i lui Ioan. <br />
                Sunt stihuri fr an, <br />
                Stihuri de groap, <br />
                De sete de ap i de foame de scrum, <br />
                Stihurile de acum. <br />
                Cnd mi s-a tocit unghia <br />
                ngereasc Am lsat-o s creasc <br />
                i nu mi-a crescut - <br />
                Sau nu o mai am cunoscut. <br />
                Era ntuneric. <br />
                Ploaia btea departe, afar. <br />
                i m durea mn ca o ghiar <br />
                Neputincioas s se strng <br />
                i m-am silit s scriu cu unghiile de la mna stng. <br />
              </div>
              <div class="section">
                <span className="title">Poezia:</span>
                <p>
                  Poezia "Flori de mucigai" se afia n deschiderea volumului
                  omonim i constituie arta poetic a lui Arghezi, concepia lui
                  despre efortul artistului i implicaiile acestuia n actul
                  creaiei, constituind -aadar - poezia programatic a acestui
                  volum, aa cum "Testament" este arta poetic din volumul
                  "Cuvinte potrivite".
                </p>
              </div>
              <div class="section">
                <span className="title">Tema poeziei:</span>
                <p>
                  Tema poeziei exprim efortul creator al artistului pentru un
                  produs spiritual i consecinelepe care le are acesta asupra
                  strilor interioare ale eului poetic, chinuit de frmntri i
                  de tulburri interioare.
                </p>

                <p>
                  Versurile nu mai sunt produsul unei revelaii, al harului
                  divin, ci al unei neliniti artistice i al setei creatoare.
                </p>
              </div>
              <div class="section">
                <span className="title">Titlul:</span>

                <p>
                  Titlul poeziei "Flori de mucigai" este un oximoron, n care
                  florile sugereaz frumuseea, puritatea, lumina, iar mucigaiul
                  semnific urtul, rul, descompunerea i ntunericul.
                </p>
                <p>
                  Oximoronul creeaz o imagine contradictorie a lumii, n care
                  valorile umane sunt degradate, alterate, lumea nchisorilor,
                  n care viaa oamenilor este supus reprimrilor,
                  restriciilor rigide.
                </p>

                <p>
                  Titlul este, n acelai timp, reprezentativ pentru inovaia
                  limbajului arghezian numit estetica urtului, o modalitate
                  artistic ntlnit n lirica european la Baudelaire, care
                  scrisese "Florile rului".
                </p>
                <p>
                  Asocierea celor dou categorii estetice contradictorii,
                  frumosul - reprezentat de floare - i urtul - sugerat de
                  mucigai - ofer titlului o expresivitate ocant i fascinanta
                  totodat prin efectele estetice.
                </p>
                <p>
                  Urtul are rolul de a evidenia imperfeciunile vieii,
                  senzaiile de aversiune i oroare care capt valori noi, ele
                  fcnd parte din existena uman.
                </p>
              </div>
              <div class="section">
                <span className="title">Structura:</span>
                <p>
                  Poezia "Flori de mucigai" este structurat n dou secvene
                  lirice inegale, prima ilustrnd crezul artistic arghezian, iar
                  cealalt neputin artistului de a crea n condiii de
                  claustrare.
                </p>

                <p>
                  <strong>Prim secven</strong> sugereaz dorina devoratoare
                  a artistului de a se exprima n versuri, fiind dominat de
                  setea de comunicare cu lumea. Poetul, ntr-o solitudine impus
                  i lipsindu-i uneltele scrisului, ncearc s zgrie
                  <span className="citat">
                    "cu unghia pe tencuiaia / Pe un prete de firid goal, / Pe
                    ntuneric" /
                  </span>
                  versurile nscute din nevoia comunicrii.
                </p>
                <p>
                  Condiiile vitrege de via i seac for creatoare,
                  <span className="citat">
                    "Cu puterile neajutate / Nici de taurul, nici de leul, nici
                    de vulturul / Care au lucrat mprejurul / Lui Luca, lui
                    Marcu i lui loan."
                  </span>
                  Enumerarea prin negaie a elementelor fabuloase ale
                  evanghelitilor, "taurul", "leul", "vulturul", creeaz o
                  imagine de mare for sugestiv privind starea de deprimare a
                  poetului nefericit n absena creaiei, n raport direct cu
                  scrierile religioase a cror esen este Absolutul.
                </p>
                <p>
                  Versurile sunt spate n sufletul poetului, sunt "stihuri fr
                  an" ce nu pot fi exprimate n viaa real, dar sunt profund
                  simite de sensibilitatea artistului:
                  <span className="citat">
                    "Stihuri de groap / De sete de ap / i de foame de scrum".
                  </span>
                </p>
                <p>
                  Harul poetic, "unghia ngereasc", este tocit de efort, nu-i
                  mai permite poetului revelaia, deoarece ea "nu a mai crescut"
                  sau, altfel spus, artistul nu se mai poate regsi n sine, nu
                  se mai percepe ca pe un creator de valori spirituale: "Sau nu
                  o mai am cunoscut".
                </p>

                <p>
                  <strong>Ultima secven</strong> amplific dezndejdea lui
                  Arghezi, care este simbolizat de atmosfera sumbr, e
                  "ntuneric", iar ploaia se aude "departe afar", ceea ce
                  provoac poetului o durere simit profund, "ca o ghiar",
                  din cauza neputinei totale de a se exprima. Nevoia de
                  comunicare a poetului cu lumea, setea de a-i dezvlui
                  tririle l silesc s scrie "cu unghiile de la mna stng".
                </p>
                <p>
                  O simbolistic strveche asociaz mna stng cu forele
                  demonice, n opoziie total cu puterea divin a creaiei.
                  nchisoarea este pentru Arghezi un fel de bolgie a Infernului
                  lui Dante, ns, n acest cadru al ororilor, frumosul nu este
                  absent.
                </p>
                <p>
                  Rul, urtul sunt numai conjuncturi ale destinului, crora
                  omul le opune aspiraia spre frumos, care poate fi regsit n
                  sine, n vis sau n sperana mplinirii. n "Flori de mucigai"
                  predomin registrele stilistice ale esteticii urtuiui, pe de
                  o parte ca inovaie lingvistic, pe de alt parte ca substan
                  a ideilor exprimate.
                </p>
              </div>
              <div class="section">
                <span className="title">Limbajul:</span>
                <p>
                  Limbajul este caracterizat prin folosirea cuvintelor care
                  ocheaz prin expresivitatea fascinant, cuvinte "urte", al
                  cror sens capt noi valori. De pild, cuvntul "mucigai"
                  este un regionalism cu aspect arhaic, dar are aici sensul
                  profund al degradrii morale, al descompunerii spirituale, cu
                  trimitere sugestiv ctre om, deoarece el nsoete cuvntul
                  "flori", care poate semnifica viaa, lumea.
                </p>

                <p>
                  Arghezi utilizeaz cuvinte din limbajul popular ori arhaisme,
                  ca "firid", "stihuri", din vocabularul religios, cum sunt
                  numele celor trei evangheliti - Luca, Marcu, loan -pentru a
                  sugera atemporalitatea strilor sufleteti de tristee,
                  dezamgire i deprimare ale poetului.
                </p>
              </div>
              <div class="section">
                <span className="title">Oximoronul:</span>
                <p>
                  Oximoronul "flori de mucigai" transmite ideea complex a
                  imperfeciunilor vieii, a condiiilor vitrege la care este
                  supus fiin uman, fapt care i provoac poetului aversiune,
                  repulsie. Metaforele argheziene poteneaz starea de disperare
                  a omului claustrat, a artistului care nu poate crea liber,
                  fiind constrns s-i reprime setea de comunicare.
                </p>

                <p>
                  Neputina creatoare a artistului este sugestiv relevat de
                  metafor "cu puterile neajutate", dei poetul avea resurse
                  spirituale profunde. Lumea nchisorii poart n ea stigmatele
                  rului care acioneaz negativ asupra naturii angelice a
                  fiinei umane, tocete s"unghia ngereasc", mpiedicnd-o
                  astfel s e exprime.
                </p>

                <p>
                  La noutatea limbajului arghezian s-a referit i Tudor Vianu,
                  care afirm: "Renovarea liricii romneti, smulgerea ei de pe
                  cile unde o fixase marea influen a poetului "Luceafrului",
                  este consecina cea mai important produs de afirmarea lui
                  Arghezi nc din al doilea deceniu al secolului nostru
                  (secolul al XX-lea - n.n.)".
                </p>
              </div>
            </div>
          </div>
        </Content>
      </div>
    );
  }
}
