import React, { Component } from "react";
import { Layout } from "antd";
import ExtraContent from "./ExtraContent";
// Ant design
const { Content } = Layout;

export default class EuNuStrivesc extends Component {
  render() {
    return (
      <div className="content_wrapper">
        <Content
          style={{
            background: "#fff",
            padding: 24,
            marginTop: "3em",
            minHeight: 280
          }}
        >
          <div className="content">
            <div className="header">
              <h1>Eu nu strivesc corola de minuni a lumii</h1>
              <h4>Lucian Blaga</h4>
            </div>

            <ExtraContent />
            <div className="body">
              <div class="section poezie">
                Eu nu strivesc corola de minuni a lumii <br />
                i nu ucid <br />
                cu mintea <br />
                tainele, ce le-ntlnesc <br />
                n calea mea <br />
                n flori, n ochi, pe buze ori morminte <br />
                Lumina altora <br />
                sugrum vraja neptrunsului ascuns <br />
                n adncimi de ntuneric, <br />
                dar eu, <br />
                eu cu lumina mea sporesc a lumii tain - <br />
                i-ntocmai cum cu razele ei albe luna <br />
                nu micoreaz, ci tremurtoare <br />
                mrete i mai tare taina nopii, <br />
                aa mbogesc i eu ntunecat zare <br />
                cu largi fiori de sfnt mister <br />
                i tot ce-i ne-neles <br />
                se schimb-n ne-nelesuri i mai mari <br />
                sub ochii mei -<br />
                cci eu iubesc <br />
                i flori i ochi i buze i morminte
              </div>
              <div class="section">
                <span className="title">Poezia:</span>
                <p>
                  Poezia Eu nu strivesc corola de minuni a lumii de Lucian
                  Blaga este o art poetic modern, alturi de Testament de
                  Tudor Arghezi i Joc secund de Ion Barbu. Poezia deschide
                  volumul de debut, Poemele luminii i are rol de manifest
                  literar.
                </p>

                <p>
                  Dac Arghezi punea n centrul artei sale poetice problema
                  limbajului i a cuvintelor potrivite, Blaga accentueaz
                  raportul dintre poet i lume i dintre poet i creaie. n
                  aceast poezie autorul face distincia
                </p>

                <p>
                  ntre cele dou tipuri de cunoatere teoretizate n lucrarea
                  Cunoaterea luciferic (1933).
                </p>

                <p>
                  <strong>Cunoaterea paradisiac</strong> este cunoaterea de
                  tip raional, care reduce misterul lumii prin intermediul
                  logicii i al intelectului.
                </p>
                <p>
                  n schimb, <strong>cunoaterea luciferic</strong> este bazat
                  pe intuiie, pe imaginaie, pe triri interioare, putnd fi
                  echivalat cu o cunoatere de tip poetic. n creaia autorului
                  se observ n mod clar opiunea pentru al doilea tip de
                  cunoatere.
                </p>
                <p>
                  n poezia Eu nu strivesc corola de minuni a lumii rolul
                  poetului nu este de a descifra tainele lumii ci de a le
                  potena prin trirea interioar i prin contemplarea formelor
                  concrete prin care ele se nfieaz.
                </p>
                <p>
                  Rolul poeziei este acela c, prin mit i simbol, elemente
                  specifice imaginaiei, creatorul s ptrund n tainele
                  Universului, sporindu-le. Creaia este un mijlocitor ntre eu
                  i lume care nu reduce ns misterul cuvntului originar.
                  Acest cuvnt poetic nu este folosit ns pentru a numi ci
                  pentru a sugera.
                </p>
              </div>
              <div class="section">
                <span className="title">Tema poeziei:</span>
                <p>
                  Tema poeziei o reprezint atitudinea poetic n faa marilor
                  taine ale Universului conform creia cunoaterea lumii este
                  posibil numai prin iubire, prin comunicare afectiv total.
                </p>
                <p>
                  Poezia este scris sub forma unei confesiuni lirice, n care
                  Lucian Blaga adopt formula lirismului subiectiv, subliniat de
                  atitudinea poetic transmis n mod direct i prin mrcile
                  lingvistice ale subiectivitii: pronumele personale la
                  persoana I singular: eu (care se repet de cinci ori pe
                  parcursul poeziei), adjectivul posesiv de persoana I: mea,
                  verbele la persoana I: nu strivesc, nu ucid, ntnlesc,
                  sporesc, mbogesc, iubesc.
                </p>
              </div>
              <div class="section">
                <span className="title">Titlul:</span>
                <p>
                  Titlul include o metafor revelatorie, corola de minuni a
                  lumii, care semnific ideea cunoaterii luciferice. Corola
                  de minuni a lumii, imagine a perfeciunii, a absolutului,
                  prin ideea de cerc, de ntreg, semnific misterele universale,
                  iar rolul poetului este de a adnci taina.
                </p>
              </div>
              <div class="section">
                <span className="title">Incipitul:</span>
                <p>
                  Incipitul reia titlul poeziei, aflndu-se ntr-o relaie de
                  sens cu versurile care ncheie arta poetic:
                  <span className="citat">
                    Eu nu strivesc corola de minuni a lumii[...] cci eu iubesc
                    / i flori i ochi i buze i morminte.{" "}
                  </span>
                </p>
                <p>
                  Faptul c aceast art poetic ncepe cu pronumele personal
                  eu atrage atenia asupra ipostazei eului liric, care este un
                  eu stihial, exacerbat.
                </p>
                <p>
                  nc din primul vers al poeziei se subliniaz poziia pe care
                  o adopt poetul n legtur cu misterele lumii, el refuznd s
                  le cunoasc n mod raional, fapt exprimat prin verbul la
                  forma negativ nu strivesc.
                </p>
                <p>
                  Ideea se menine pe parcursul operei prin intermediul altor
                  verbe semnificative: nu ucid, sporesc, mbogesc,
                  iubesc.
                </p>
              </div>
              <div class="section">
                <span className="title">Compoziie:</span>
                <p>
                  Poezia este alctuit din trei secvene poetice, prima i
                  ultima aflndu-se ntr-o relaie de simetrie deoarece amndou
                  descriu poziia poetului n legtur cu creaia i misterul.
                </p>
                <p>
                  Se remarc ns i relaia de opoziie dintre aceste dou
                  secvene i a doua, care i include pe creatorii care se
                  folosesc de cunoaterea paradisiac (lumina altora).
                </p>

                <p>
                  <strong>Prima secven </strong>ofer o definiie a creaiei,
                  poezia nsemnnd pentru Blaga intuirea n particular (eu nu
                  strivesc) a universalului, a misterului. De asemenea, este
                  prezentat atitudinea poetic fa de acest mister, exprimat
                  prin verbele la forma negativ: nu strivesc, nu ucid.
                </p>
                <p>
                  Eul liric refuz cunoaterea paradisiac, raional (cu
                  mintea) a misterului, care ar duce de fapt la dispariia
                  acestuia. Drumul creaiei pe care i-l alege este exprimat
                  prin substantivul dublat de un adjectiv pronominal posesiv
                  (care exprim tocmai ideea de opiune): calea mea.
                </p>

                <p>
                  Minunile corolei sunt descrise prin patru metafore-simbol,
                  care se refer la temele creaiei blagiene: n flori, n
                  ochi, pe buze ori morminte. Florile simbolizeaz viaa,
                  efemeritatea, dar i frumosul, ochii cunoaterea, contemplarea
                  poetic a lumii, buzele iubirea, dar i rostirea poetic, iar
                  mormintele moartea, eternitatea, dou teme care au fost
                  asociate de toi poeii cu misterul datorit imposibilitii
                  de a le cunoate integral.
                </p>

                <p>
                  <strong>A doua secven</strong> este construit pe relaia de
                  opoziie dintre opiunea poetului i opiunile altor scriitori
                  n ceea ce privete revelarea misterului. Aceast opoziie
                  semnific de fapt antitez dintre cele dou tipuri de
                  cunoatere, paradisiac i luciferic.
                </p>

                <p>
                  Diferena dintre cele dou atitudini poetice este redat la
                  nivelul textului de ctre pronumele personal eu, adjectivul
                  pronominal posesiv mea (lumina mea) i adjectivul
                  nehotrt altora (lumina altora). Metafora luminii, care
                  este metafora central a volumului Poemele luminii,
                  simbolizeaz cunoaterea.
                </p>

                <p>
                  Cele dou tipuri de cunoatere sunt redate prin asocierea
                  acestor elemente de opoziie cu verbe sugestive care le pun i
                  mai bine n eviden: Lumina altora / sugrum vraja
                  neptrunsului ascuns, n timp ce eul liric blagian sporete
                  a lumii tain / [...] nu micoreaz, ci tremurtoare /
                  mrete i mai tare taina nopii.
                </p>

                <p>
                  Elementele care in de mister, de imaginarul poetic sunt
                  sugerate de alte cuvinte-simbol, care fac parte din cmpul
                  semantic al misterului: neptrunsul ascuns (epitet
                  metaforic, inversiune), adncimi de ntuneric (metafor), a
                  lumii tain, taina nopii (metafor), ntunecat zare
                  (epitet, inversiune), largi fiori de sfnt mister (epitete
                  metaforice), ne-neles, ne-nelesuri i mai mari. Crezul
                  poetic este reluat n ultima secven, aflat n relaie de
                  simetrie cu prima.
                </p>

                <p>
                  <strong>Finalul</strong> are un rol conclusiv (folosirea
                  conjunciei cci nu este ntmpltoare), n care poetul reia
                  imaginea corolei alctuit din elementele misterului poetic:
                  <span className="citat">
                    cci eu iubesc / i flori i ochi i buze i morminte.
                  </span>
                </p>
                <p>
                  Cunoaterea poetic este att un act de contemplaie (aa cum
                  reiese din ultimele dou versuri ale celei de a doua secvene:
                  se schimb-n ne-nelesuri i mai mari / sub ochii mei), ct
                  i un act de iubire: cci eu iubesc.
                </p>
              </div>
            </div>
          </div>
        </Content>
      </div>
    );
  }
}
