import React, { Component } from "react";
import { Layout } from "antd";
import ExtraContent from "./ExtraContent";
// Ant design
const { Content } = Layout;

export default class LeoaicaTanara extends Component {
  render() {
    return (
      <div className="content_wrapper">
        <Content
          style={{
            background: "#fff",
            padding: 24,
            marginTop: "3em",
            minHeight: 280
          }}
        >
          <div className="content">
            <div className="header">
              <h1>Leoaica tnr, iubirea</h1>
              <h4>Nichita Stanescu</h4>
            </div>

            <ExtraContent />
            <div className="body">
              <div class="section poezie">
                <p>
                  Leoaica tnr, iubirea <br />
                  mi-ai srit n fa. <br />
                  M pndise-n ncordare <br />
                  mai demult. <br />
                  Colii albi mi i-a nfipt n fa, <br />
                  m-a mucat leoaica, azi, de fa. <br />
                </p>

                <p>
                  i deodata-n jurul meu, natura <br />
                  se fcu un cerc, de-a-dura, <br />
                  cnd mai larg, cnd mai aproape, <br />
                  ca o strngere de ape. <br />
                  i privirea-n sus ini, <br />
                  curcubeu tiat n dou, <br />
                  i auzul o-ntlni <br />
                  tocmai lng ciocrlii. <br />
                </p>

                <p>
                  Mi-am dus mna la sprncean, <br />
                  la tmpl i la brbie, <br />
                  dar mna nu le mai tie. <br />
                  i alunec-n netire <br />
                  pe-un deert n strlucire, <br />
                  peste care trece-alene <br />
                  o leoaic armie <br />
                  cu micrile viclene, <br />
                  nc-o vreme, <br />
                  i-nc-o vreme... <br />
                </p>
              </div>
              <div class="section">
                <span className="title">Definiia curentului:</span>
                <p>
                  Denuminrea de neomodernism se datoreaz viziunii moderne a
                  artitilor abordat mai trziu,n deceniul al 6-lea n secolul
                  XX,cnd modernismul interbelicse ncheiase cu cteva decenii.
                </p>

                <p>
                  Poeii acestei perioade se ntorc,dup perioada ntunecat a
                  realismului socialist,la metafore subtile,la ironie i la
                  temele filosofice ale poeziei interbelice,la
                  mit,intelectualism.
                </p>

                <p>
                  Ei vor revigora poezia,determinnd-o s revin la discursul
                  liric interbelic,laformulelede expresie metaforice,la imagini
                  artistice,a refleciei filosofice,dovada c realismul social
                  este prsit i poezia se liricizeaz, deschizndu-se
                  perspectiva unui nou modernism adic neomodernismul.
                </p>
              </div>
              <div class="section">
                <span className="title">Trsturi:</span>
                <p>
                  Trsturi ale neomodernismului prezente n opera lui Stnescu
                  sunt mprirea inegal a strofelor : poezia prezint trei
                  strofe cu cte 6,8 i 10 versuri,textul este scris n vers
                  alb,rima este ntmpltoare i ritmul inegal.Poezia abund n
                  mrci ale subiectivitii : "m-a mucat".
                </p>

                <p>
                  Modernismul stanescian rezid n originalitatea
                  comparaiei-metafor a iubirii materializat ntr-o "leoaic
                  tnr",care pndete cu ncordare pn i atac victima
                  direct "n fa",mucnd aprig cu "colii albi",precum
                  felinele nemiloase,nfricotoare.
                </p>
              </div>
              <div class="section">
                <span className="title">Apariie:</span>
                <p>
                  "Leoaic tnr,iubirea" aparine primei etape de creaie al
                  celui de-al doilea volum al poetului intitulat "O viziune a
                  sentimentelor" aprut n anul 1964, volum ce abordeaz
                  sentimentul dragostei vzut ca o stare de graie.
                </p>
              </div>
              <div class="section">
                <span className="title">Tema:</span>
                <p>
                  Tema: pozeiei o constituie "iubirea",privit ca "regina" a
                  sentimentelor i neleas ca stare de graie dar i c for
                  demiurgic,ce nvlete asemenea unui animal de prad n
                  spaiul sensibilitii poetice,capabil s l determine pe
                  ndrgostit s reordoneze universul i s schimbe raportul
                  eului poetic cu lumea exterioar i cu inele sau totodat.
                </p>
              </div>
              <div class="section">
                <span className="title">Titlul:</span>
                <p>
                  Titlul poeziei este metaforic i simbolic sugernd tema poeiei
                  i anume iubirea.Structural titlul este alctuit dintr-o
                  apozitie
                </p>

                <p>
                  Titlul este exprimat pintr-o metafor,care sugereaz extazul
                  poetic la apariia neateptat a iubirii,perceput ca un
                  animal agresiv construcia apoziional "leoaic tnr" este
                  antepusa substantivului "iubirea",care devine simbolul central
                  al textului i despre care prin epitetul "tnra" se sugereaz
                  fora realizrii individului prin naterea sentimentului etern
                  al erosului.
                </p>

                <p>
                  Prin alctuirea celor trei cuvinte,ncercarea poetului este
                  accea de a face cunoscut nc din titlu trsturile iubirii:
                  este tnr,instinctual,slbatic,feroce,spontan i plin de
                  frumusee.Se nelege c "leoaic"/iubirea atac surprinztor
                  pe neateptate.
                </p>
              </div>
              <div class="section">
                <span className="title">Compoziia exterioar:</span>
                <p>
                  Compoziional poezia este alctuit din 3 strofe cu un numr
                  diferit de versuri (6,8,10).Versurile sunt inegale c msur
                  i rima este accidental.Structura creeaz impresia de
                  spontaneitate n dezvluirea sentimentelor,discursul liric
                  fiind eliberat de toate oprelitile,n afara unui foarte
                  firesc ritm care cadeneaz micarea afectiv.
                </p>
              </div>

              <div class="section">
                <span className="title">Structura de adncime:</span>
                <p>
                  Discursul liric asemenea strofelor este structurat n 3
                  secvene :
                </p>
                <p>
                  <strong>Prima secven</strong> corespunde primei strife.Aici
                  prin intermediul metaforelor,al personificrilor i
                  comparaiilor ne este prezentat modul n care vntorul i
                  prinde prada.
                </p>
                <p>
                  Sentimentul de iubire i-a forma unei tinere leoaice
                  agresive,care nvlete n sufletul poetului cu o for
                  devastatoare,sfiindu-i cu colii ei albi fiina i
                  provocndu-i durere: "Leoaic tnr,iubirea/Mi-ai srit n
                  fr/M pndise-n ncordare/Mai demult/Colii albi mi i-a
                  nfipt n fa/M-a mucat leoaica azi de faa". Aceast
                  apariie feroce i las urme adnci pe faa
                  individului,metafora semnelor semnificnd c cel atins de
                  sentiment se difereniaz de ceilalti.
                </p>
                <p>
                  Personificarea iubirii creeaz sugestia nceputului tulburtor
                  al dragostei adolescentine,care sperie prinspontaneitatea
                  ei,aventura iubirii nedepinznd de voita celui atacat.Dac n
                  prima secven dragostea este vzut din exterior.
                </p>
                <p>
                  <strong>Cea de-a doua secven</strong> prezentat-n
                  urmtoarea strof,dezvluie iubirea vzut din interior i
                  accentueaz efectul psihologic al neateptatei ntlniri.
                </p>
                <p>
                  De data aceasta individul trece ntr-o alt dimensiune a
                  existenei,iubirea poziionndu-l n centrul Universului.Fora
                  agresiv a iubirii pare s reordoneze lumea dup propriile
                  legi,ntr-un joc al cercurilor concentrice,ca simbol al
                  perfeciunii,eul liric simndu-se n aceast nou univers un
                  adevrat "Axis Mundi".
                </p>

                <p>
                  <strong>Secvena a treia</strong> (corespunde ultimeu strofe)
                  revine a ideea central a poeziei,iubirea,vzut acum ca o
                  for care pustiete fiina poetului,transformat ntr-un
                  "deert n strlucire" peste care "trece alene" puternic
                  "leoaic armie/cu micrile viclene".
                </p>

                <p>
                  Micrile unduioase,lente i incontiente ale fiarei(ce
                  "alunec-n netire")i fac pe cei doi s se contopeasc,fiina
                  iubit prnd a se transforma ntr-un deert infinit peste
                  care trece sentimentul iubirii n ipostaza de "leoaic
                  armie".
                </p>

                <p>
                  Finalul d de neles faptul c animalul se afl iari la
                  panda.Dincolo de metafora acestei relaionari se afl un
                  adevr general valabil: indiferent de momentul istoric n care
                  triete,individul se va ntlni inevitabil cu sentimentul de
                  iubire,care va aciona asupra lui spontan i impulsiv.
                </p>
              </div>
            </div>
          </div>
        </Content>
      </div>
    );
  }
}
